@import url(https://fonts.googleapis.com/css2?family=Graduate&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.container {
  background: rgb(42, 42, 42);
  border-radius: 10px;
  font-family: "Graduate", cursive;
}

.container h3 {
  font-size: 50px;
  color: tomato;
  width: 400px;
  text-align: center;
  margin: 0 auto;
  padding: 10px;
}

.small {
  color: #fff;
  font-size: 30px;
}

.playerStyle h1 {
  background: tomato;
  color: #fff;
  border-radius: 10px;
  font-size: 30px;
  height: 50px;
  line-height: 40px;
}

.playerStyle h3 {
  margin: 20px 0;
  background: rgb(255, 228, 223);
  border-radius: 10px;
  line-height: 40px;
  font-size: 25px;
}

.line {
  width: 200px;
  background: rgb(255, 228, 223);
  height: 5px;
  border-radius: 10px;
  margin: 0 auto;
}

.listStyle li {
  list-style-type: none;
  margin: 10px 0;
  font-size: 20px;
  text-align: justify;
}

.icon {
  margin-right: 10px;
}

.show-case {
  display: flex;
  justify-content: center;
}

.player-container {
  width: 60%;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  margin: 10px;
  border-right: 5px solid rgb(255, 228, 223);
}

.team-container {
  width: 30%;
  margin: 10px;
  text-align: center;
}

